.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 48px;
}

.TodoSearch {
  background: #88d4fa;
  border-radius: 50px;
  border: 2px solid #87a8ea;
  margin: 0 0;
  height: 64px;
  width: 500px;
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: white;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
  color: white;
  font-family: 'Montserrat';
  font-weight: 200;
}

.TodoSearch::placeholder {
  color: white;
  font-family: 'Montserrat';
  font-weight: 200;
}

.TodoSearch:focus {
  outline-color: #1857d6;
}

ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}

.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    
    /* background-color: #CCC; */
  }
  .Icon-container--check {
    position: absolute;
    left: 12px;
    background-color: white;
    border-radius: 50px;
    box-shadow: 1px 3px 10px rgb(29, 31, 0);

  }
  .Icon-container--delete {
    position: absolute;
    background-color: white;
    right: 10px;
    border-radius: 50px;
    box-shadow: 1px 3px 10px rgb(29, 31, 0);
  }
  
  .Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
  }
  .Icon-container:hover .Icon-svg--check {
    fill: #4caf50;
  }
  .Icon-container:hover .Icon-svg--delete {
    fill: red;
  }
.TodoItem {
  background-color: #247a96a0;
  width: 500px;
  border-radius: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  color: white;
  line-height: 24px;
  font-weight: 400;
}

.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
  background-color: white;
  border-radius: 50px;
  box-shadow: 1px 3px 10px rgb(29, 31, 0);
}

.Icon-check:hover{
  color: white;
  background-color: rgb(67, 208, 24);
}

.Icon-check--active {
  color: #ffffff;
  background-color: rgb(67, 208, 24);
  border-radius: 50px;
  box-shadow: 1px 3px 10px rgb(29, 31, 0);

}

.Icon-delete {
  font-family: 'Courier New', Courier, monospace;
  position: absolute;
  right: 25px;
  background-color: white;
  border-radius: 50px;
  box-shadow: 1px 3px 10px rgb(29, 31, 0);
}
.Icon-delete:hover {
  color: white;
  background-color: red;
}

form {
    width: 90%;
    max-width: 500px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    resize: none;
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }

  .TodoForm-button--add:hover {
    background: #22afd6;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    color: white;
    background: rgba(235, 34, 34, 0.829);
  }

  .TodoForm-button--cancel:hover {
    color: white;
    background: rgba(177, 25, 25, 0.829);
  }
.CreateTodoButton {
  background-color: #61DAFA;
  box-shadow: 1px 3px 10px rgb(29, 31, 0);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65px;
  width: 65px;
  z-index: 1;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  transition: .3s ease;
  background-color: coral;
}

.ModalBackground {
  background: rgba(32,35,41,.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.LoadingTodo-container {
    border-radius: 50px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
  
.LoadingTodo-text {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 24px 0 24px 24px;
    color: darkgrey;
    width: 500px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 200;
    text-align: center;
}
  
.LoadingTodo-container {
    background:linear-gradient(90deg, rgba(250,250,250,1), rgb(200,199,199));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
    
}

@keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
    
}
  

